<template>
  <v-container>
    <p v-for="(item, index) in tests">{{item.Name}}</p>
  </v-container>
</template>

<script>
import http from "../http-common";

export default {
  data(){
    return{
      tests:[]
    }
  },
  methods:{
    dataTest(){
      http
          .get("/about/test")
          .then(response=>{
            this.tests = response.data;
            console.log(response.data);
          })
          .catch(e=>{
            console.log(e);
          })
    }
  },
  mounted(){
    this.dataTest();
  }
}
</script>